import React, {Component} from 'react';

class Contact extends Component{
    state ={
        name:"",
        email:"",
        msg:""
    }
   hundleChangeName= (e) =>{
       let name= e.target.value;
       this.setState({
           name:name
       })
   }  
   hundleChangeEmail= (e) =>{
    let email= e.target.value;
    this.setState({
        email:email
    })
}  
hundleChangeMsg= (e) =>{
    let msg= e.target.value;
    this.setState({
        msg:msg
    })
}  
    hundleSubmit = (e) =>{
        e.preventDefault();
        let name= this.state.name;
        let email = this.state.email;
        let msg= this.state.msg;
        
        alert( 'bonjour '+ name + ' ton mail est : '+ email + ' tonn message est : '+msg);
    }

    render(){
        return (
       <div className="welcome">
            
               <div className="contenu">
               <form className="row" onSubmit={this.hundleSubmit}> 
                    <div className="form-group col-md-6 ">
                        <label htmlfor='name'>Name: </label>
                        <input type='text' className="form-control"  placeholder="type your name" onChange={this.hundleChangeName} required/>
                    </div>
                    <div className="form-group col-md-6 ">
                        <label htmlfor='email'>Email: </label>
                        <input type='email' className="form-control" id="email" placeholder="type your Email" onChange={this.hundleChangeEmail} required/>
                    </div>
                    <div className="form-group col-md-12 ">
                        <label htmlfor='msg'>Message: </label>
                        <input type='text' className="form-control"id="msg" placeholder="type your message" onChange={this.hundleChangeMsg} required/>
                    </div>
                    <div className="row text-center">
                        
                        <input type='submit' className="btn btn-primary btn-lg" value="Envoyer" />
                    </div>
                   
                </form>
               </div>
           
       </div>
        );
    }
   
}

export default Contact; 